import React, { useState, useRef, useEffect } from 'react';
import ReactDOM from 'react-dom/client';
import { send } from 'emailjs-com';
import AOS from "aos";
import "aos/dist/aos.css";
import './index.css';

function App() {
  const name = useRef()
  const email = useRef()
  const message = useRef()
  const [alert, setAlert] = useState('')
  const [load, setLoad] = useState(false)

  useEffect(() => {
    AOS.init({
      duration: 700,
      once: true
    });
  }, []);

  const sendEmail = (e) => {
    e.preventDefault();
    setLoad(true);
    send('service_lnna21m', 'template_0m967db', 
    {name: name.current.value, email: email.current.value, message: message.current.value}, 
    'user_M6g4nRDcaLWFLkrywRpRY')
    .then((response) => {
      setAlert('succeed');
      name.current.value = '';
      email.current.value = '';
      message.current.value = '';
      setLoad(false);
    })
    .catch((err) => {
      setAlert('failed');
      setLoad(false);
    });
  }

  useEffect(() => {
    setTimeout(()=>{
        setAlert('')
        return () => {setAlert('')};
    }, 3000)
  }, [alert])

  const Home = () => {
    return (
      <div className="box home">
        <div className="text" data-aos="zoom-in">
          <h1>hi, i'm<span> indra</span></h1>
          <h1>a<span> web developer</span></h1>
          <h1>let's build<span> great </span>websites</h1>
        </div>
      </div>
    );
  }

  const About = () => {
    return (
      <div className="box about">
        <div className='image' data-aos="fade-up">
          <img src={require('./assets/profile.svg').default} alt="Profile" loading='lazy' />
        </div>
        <div className="text" data-aos="fade-left">
          <h1 className='title'>who am i?</h1>
          <p>My name is<span> Muhammad Laksmana Indra</span>, {new Date().getFullYear()-2002} years old, live in Indonesia. 
          I am a Frontend, Backend and Fullstack Developer who masters various programming languages 
          to build a modern and stable website with creativity and passion.</p>
        </div>
      </div>
    );
  }

  const Work = () => {
    const Frame = ({src, alt, url, text}) => {
      return (
        <div className='image' data-aos="zoom-out">
          <img src={require(`./assets/${src}.jpg`)} alt={alt} loading='lazy' />
          <a href={url} target="_blank" rel="noopener noreferrer"><i className="fas fa-globe icon"></i></a>
          <h2>{text}</h2>
        </div>
      );
    }

    return (
      <div className="box work">
        <h1 className='title' data-aos="fade-right">some of my works</h1>
        <div className='list'>
          {/* <Frame src='medsos' alt='Medsos App' url='http://inmu-medsos.dgrande.com' text='Social Media' />
          <Frame src='property' alt='Property Management' url='http://inmu-property.dgrande.com' text='Property Management' /> */}
          <Frame src='music' alt='Music App' url='https://indra-19.github.io/music-app' text='Music' />
          <Frame src='notes' alt='Notes' url='https://indra-19.github.io/notes-app' text='Notes' />
          <Frame src='quotes' alt='Quotes' url='https://indra-19.github.io/quotes-app' text='Quotes' />
          <Frame src='weather' alt='Weather App' url='https://indra-19.github.io/weather-app' text='Weather App' />
          <Frame src='stopwatch' alt='Stopwatch' url='https://indra-19.github.io/stopwatch-app' text='Stopwatch' />
          <Frame src='drum' alt='Drum Machine' url='https://indra-19.github.io/drum-machine-app' text='Drum Machine' />
          <Frame src='count' alt='Count App' url='https://indra-19.github.io/count-app' text='Count App' />
          <Frame src='tictactoe' alt='Tic Tac Toe' url='https://indra-19.github.io/tic-tac-toe' text='Tic Tac Toe' />
          <Frame src='netflix' alt='Netflix Clone' url='https://indra-19.github.io/netflix-clone' text='Netflix Clone' />
          <Frame src='imgfinder' alt='Img Finder' url='https://indra-19.github.io/image-finder' text='Image Finder' />
          <Frame src='movie' alt='Movie Information' url='https://indra-19.github.io/movie-app' text='Movie Information' />  
        </div>
      </div>
    );
  }

  const Contact = () => {
    return (
      <div className="box contact">
        <div className="text" data-aos="fade-right">
          <h1 className='title'>let's talk</h1>
          <p><span>Interested in me? </span>Or do you have any questions, 
          complaints, suggestions and criticisms about me?</p>
          <a href="https://www.linkedin.com/in/muhammad-laksmana-indra-3913b422b" target="_blank" rel="noopener noreferrer"><i className="fab fa-linkedin icon"></i></a>
          <a href="https://github.com/indra-19" target="_blank" rel="noopener noreferrer"><i className="fab fa-github-square icon"></i></a>
          <a href="https://twitter.com/mlaksmanaindra" target="_blank" rel="noopener noreferrer"><i className="fab fa-twitter-square icon"></i></a>
          <a href="https://www.instagram.com/mlaksmanaindra" target="_blank" rel="noopener noreferrer"><i className="fab fa-instagram-square icon"></i></a>
        </div>
        <form onSubmit={(e) => sendEmail(e)}>
          <label>Name</label>
          <input type='text' ref={name} required />
          <label>Email</label>
          <input type='email' ref={email} required />
          <label>Message</label>
          <textarea rows='5' ref={message} required />
          {load ? 
          <button className='dis' disabled>Sending...</button>
          : 
          <button type='submit'>Send</button>}
        </form>
      </div>
    );
  }

  const Footer = () => {
    return (
      <div className="box footer">
        <h3>Thank you for visiting, Have a nice day</h3>
        <h3><i className="far fa-copyright icon"></i> 2022 Muhammad Laksmana Indra</h3>
      </div>
    );
  }

  return (
    <div className="container">
      {alert === 'succeed' ? <div className='alert'><h3>Successfully sent</h3></div> : 
      alert === 'failed' ? <div className='alert'><h3>Failed to send</h3></div> : []}
      <Home /><hr className='right' />
      <About /><hr className='left' />
      <Work /><hr className='right' />
      <Contact /><hr className='left' />
      <Footer />
    </div>
  );
}

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>
);
